
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
























































































.sound-tracks {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.sound-clip{
  /* border: 2px solid salmon; */
  background: darkcyan;
  color: white;
  border-radius: 50%;
  margin: 20px;
  width: 100px;
  height: 100px;
}
.sound-clip span {
  display: block;
  line-height: 18px;
  font-size: 18px;
  margin-top: 40px;
}
.user-options {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.option-label {
  border-radius: 5px;
  border: 2px solid salmon;
  display: block;
  width: 200px;
  height: 70px;
  margin: 20px;
  cursor: pointer;
  margin-top: 50px;
  font-size: 18px;
}
.option-label.active {
  background: salmon;
  color: white;
}
.option-label span {
  /* border: 1px solid; */
  display:block;
  height: 100%;
  padding-top: 25px;
}
.option-value {
  display: none;
}
#nextBtn{
  display: block;
  border: none;
  border-radius: 3px;
  background: darkcyan;
  color: white;
  width: 200px;
  height: 70px;
  margin: 20px auto;
  margin-top: 80px;
  cursor: pointer;
  font-size: 18px;
}
